<template>
  <div class="box">
    <div class="box_bore">
      <div class="psd_title">乘客信息/Traveler</div>
      <div class="psd_cont" v-if="$store.state.choose_psg.length == 0 && list.length == 0">
        <div><span>请至少添加一名乘客</span>
          <span>Please add at least one passenger</span>
        </div>
      </div>
      <div class="psd_cont" v-for="(v, i) in $store.state.choose_psg" :key="i">
        <div>
          <div class="user_info">
            <van-checkbox ref="Checkbox" v-model="checked" shape="square">{{
              v.FltPaxnameDom
            }}</van-checkbox>
            <span class="contact">
              <div class="phone">
                <span>手机号/Mobile:</span>
                <span>{{ Xplus(v.MobileNo1, 3, 4) }}</span>
              </div>
              <div class="id_no">
                <span>证件号/ID No:</span>
                <span>{{ Xplus(cryptox.decrypt(v.IdNo), 4, 4) }}</span>
              </div>
            </span>
            <span class="delet">
              <van-icon name="delete-o" size="18" @click="deletep(i)" />
            </span>
          </div>
        </div>
      </div>
      <div class="add" @click="showPicker4 = true">
        <div>
          <van-icon name="plus" size="25" />
        </div>
      </div>
      <div v-if="$route.path == '/internationalflight'" style=" font-size: 10px;
          color: #ff4141;
          text-align: left;
          padding-bottom: 12px;
          font-weight: 600;
        ">
        <span>非中国大陆始发行程，请联系您的旅行顾问咨询优惠报价。 For origins
          outside mainland China, please contact our TC to get favorable
          fare</span>
      </div>
    </div>

    <!-- 添加弹出层 -->
    <van-popup class="select_popup" v-model:show="showPicker4" round position="bottom" :style="{ height: '55%' }">
      <div class="psd_">
        <div class="head_title">
          添加乘机人/Add Traveler
          <span><van-icon name="close" size="23" @click="showPicker4 = false" /></span>
        </div>

        <van-search v-model="$store.state.searchpsg" placeholder="查询乘机人/Searache Traveler" v-if="!searchpsg_more" />
        <van-checkbox-group v-if="!searchpsg_more" class="select_popup_psg" v-model="$store.state.choose_psg_p" ref="checkboxGroup">
          <van-checkbox v-for="(v, i) in psg_search" :key="i" :name="v" shape="square">
            <div class="add_traveler">
              <i>
                <span>{{ v.FltPaxnameDom }} -- {{ v.FltPaxnameInt }}</span>
              </i>
              <div class="van-flex">
                <i class="van-colx">
                  <span>手机号/Mobile No</span>
                  <span>证件号/ID No</span>
                </i>
                <i class="van-colx">
                  <span>{{ Xplus(v.MobileNo1, 3, 4) }}</span>
                  <span v-if="v.IdNo != null">{{
                    Xplus(cryptox.decrypt(v.IdNo), 4, 4)
                  }}</span>
                  <span v-else>not data</span>
                </i>
                <i class="" @click="edit(i)">
                  <img src="../../assets/image/choose.png" alt="" />
                </i>
              </div>
            </div>
            <!-- <van-row span="10">{{ v.FltPaxnameDom }}</van-row>
              <van-col span="6">手机号 {{ Xplus(v.MobileNo1, 3, 4) }}</van-col>
              <van-col span="6">证件号 {{ Xplus(v.IdNo, 2, 2) }}</van-col> -->
            <!-- <van-col span="2">{{ v.JobTitle }}</van-col> -->
          </van-checkbox>
        </van-checkbox-group>
        <div class="lfp2_btn">
          <div class="lfp2_btn_y" @click="edit()">
            <span>新增乘机人<br />New Traveler</span>
          </div>
          <div class="lfp2_btn_n" @click="add_psg">确认<br />Confirm</div>
        </div>
      </div>
    </van-popup>

    <van-popup class="select_popup" v-model:show="showPicker5" round position="bottom" :style="{ height: '45%' }">
      <div class="edit">
        <div class="head_title">
          {{ title }}
          <span><van-icon name="close" size="23" @click="
                showPicker5 = false;
                showPicker4 = true;
              " /></span>
        </div>
        <div class="from">
          <van-form @submit="on_save_psg">
            <div class="from_box">
              <div class="from_item">
                <van-field class="from_i_field" v-model="eidt_p.FltPaxnameDom" name="姓名" label="姓名/Name" placeholder="姓名/Name" :rules="[{ required: true, message: '请填写姓名' }]" />
              </div>
              <div class="from_item">
                <van-field class="from_i_field" v-model="eidt_p.idtype" name="证件类型" label="证件类型/ID Type" readonly placeholder="证件类型" @click="idtype_s = true" :rules="[{ required: true, message: '请选择证件类型' }]" />
              </div>
            </div>
            <div class="from_box">
              <div class="from_item">
                <van-field class="from_i_field" v-model="eidt_p.IdNo" name="证件号" label="证件号/ID No" placeholder="证件号" :rules="[{ required: true, message: '请填写证件号' }]" />
              </div>
              <div class="from_item">
                <van-field class="from_i_field" v-model="eidt_p.MobileNo1" name="手机号" label="手机号/Mobile" placeholder="手机号" :rules="[{ required: true, message: '请填写手机号' }]" />
              </div>
            </div>
            <div class="from_box">
              <div class="from_item">
                <van-field class="from_i_field" v-model="getSex" name="性别" label="性别/Gender" readonly placeholder="性别" @click="idtype_sex = true" :rules="[{ required: true, message: '请选择证件性别' }]" />
              </div>
              <div class="from_item">
                <van-field class="from_i_field" v-model="eidt_p.EmailCompany" name="邮箱" label="邮箱/Email" placeholder="邮箱" />
              </div>
            </div>
            <div style="margin: 16px">
              <van-button round block type="info" native-type="submit" color="linear-gradient(180deg, #0792DF 0%, #035ABB 99%, #0359BB 100%)">保存<br />Save</van-button>
            </div>
          </van-form>
        </div>
      </div>
    </van-popup>
    <van-popup class="select_popup" v-model:show="idtype_s" round position="bottom">
      <van-picker show-toolbar allow-html confirm-button-text="确认/confirm" cancel-button-text="取消/cancel" :columns="get_this_list(indexidtypes, idtype_se)" @confirm="choose_idt" @cancel="idtype_s = false" @change="change_idt" />
    </van-popup>
    <van-popup class="select_popup" v-model:show="idtype_sex" round position="bottom">
      <van-picker show-toolbar allow-html confirm-button-text="确认/confirm" cancel-button-text="取消/cancel" :columns="sex_list" @confirm="choose_sex" @cancel="idtype_sex = false" />
    </van-popup>
  </div>
</template>

<script>
import crypto from "../../utils/encryp.js";
import { showDialog } from 'vant';
import axios from "../../utils/axiosLogin.js";
export default {
  name: 'psgcont',
  data () {
    return {
      showPicker4: false,
      showPicker5: false,
      idtype_s: false,
      idtype_sex: false,
      searchpsg_more: false,
      checked: true,
      eidt_p: [],
      list: [],
      cryptox: crypto,
      idtype_se: "",
      indexidtypes: [
        { value: "身份证", text: "身份证/Mainland Chinese ID Card" },
        { value: "护照", text: "护照/PASSPORT" },
        { value: "大陆居民往来港澳通行证", text: "大陆居民往来港澳通行证/Entry-Exit Permit for HK/Macau" },
        { value: "港澳居民来往内地通行证", text: "港澳居民来往内地通行证/Mainland Travel Permit for HK/Macau Residents" },
        { value: "台湾居民往来大陆通行证", text: "台湾居民往来大陆通行证/Mainland Travel Permit for Taiwan Residents" },
        { value: "港澳台居民居住证", text: "港澳台居民居住证/Residence Permit for Hong Kong, Macau, and Taiwan Residents" },
        { value: "其他证件类型", text: "其他证件类型/Other types of documents" }
      ],
      sex_list: [
        { text: "男", value: "M" },
        { text: "女", value: "F" },
      ],
    }
  },
  computed: {
    psg_search () {
      let str = this.$store.state.searchpsg;
      if (!str) return this.$store.state.psglist; //获取用户数据返回

      let a = this.$store.state.psglist.filter((v) => {
        // console.log("222222", this.$store.state, str);
        // console.log(v.IdNo);IdNo有些是null indexOf无法判断

        return (
          v.FltPaxnameDom.indexOf(str) != -1 ||
          v.FltPaxnameInt.indexOf(str) != -1 ||
          v.MobileNo1.indexOf(str) != -1
        );
      });
      return a;
    },
    getSex: {
      get () {
        return this.sexx(this.eidt_p.Sex);
      },
      set (value) {
        console.log(value);
        this.eidt_p.Sex = value;
      },
    },
  },
  created () {
    this.dataCollationPassenger()
  },
  methods: {
    Xplus (str = "", frontLen, endLen) {
      if (!str) return "";
      var len = str.length - frontLen - endLen;
      var xing = "";
      if (len.length != 0) xing = "****";
      return (
        str.substring(0, frontLen) + xing + str.substring(str.length - endLen)
      );
    },
    // 数据整理  显示首个乘客
    dataCollationPassenger () {
      let old_booker = {};
      if (sessionStorage.getItem("psglist_json")) {
        old_booker = JSON.parse(sessionStorage.getItem("psglist_json"));
        old_booker.psglist.filter((v) => {
          v.idtype = "身份证";
          // console.log(v)
          v.IdCardNo = "";
          if (v.IdNo) {
            if (v.IdNo == v.PassportNo) {
              v.idtype = "护照";
            } else if (v.IdNo == v.HkmcEepNo) {
              v.idtype = "大陆居民往来港澳通行证";
            } else if (v.IdNo == v.ReentryPermitNo) {
              v.idtype = "港澳居民来往内地通行证";
            } else if (v.IdNo == v.MtpNo) {
              v.idtype = "台湾居民往来大陆通行证";
            } else if (v.IdNo == v.gatIdNo) {
              v.idtype = "港澳台居民居住证"
            } else {
              v.IdCardNo = v.IdNo;
              // v.idtype = '身份证';
              // if(!(card&&card =='护照')){
              //     v.IdCardNo = v.IdNo;
              // }
            }
          } else {
            if (v.PassportNo) {
              v.idtype = "护照";
              v.IdNo = v.PassportNo;
            } else if (v.HkmcEepNo) {
              v.idtype = "港澳通行证";
              v.IdNo = v.HkmcEepNo;
            } else if (v.ReentryPermitNo) {
              v.idtype = "港澳居民来往内地通行证";
              v.IdNo = v.ReentryPermitNo;
            } else if (v.MtpNo) {
              v.idtype = "台湾居民往来大陆通行证";
              v.IdNo = v.MtpNo;
            } else if (v.gatIdNo) {
              v.idtype = "港澳台居民居住证";
              v.IdNo = v.gatIdNo;
            }
          }
          return v.Identification, v.idtype;
        });
      }

      if (
        this.$store.state.psglist.length == 0 &&
        old_booker.psglist &&
        old_booker.psglist.length > 0
      ) {
        //给store里的psglist赋值
        this.$store.commit("setpsglist", old_booker.psglist);
      }
      if (sessionStorage.getItem("choose_psg")) {
        let f1 = JSON.parse(sessionStorage.getItem("choose_psg"));
        // console.log(555, f1);
        this.$store.commit("choose_psg_m", f1);
      }
      if (sessionStorage.getItem("choose_psg_more")) {
        let f2 = JSON.parse(sessionStorage.getItem("choose_psg_more"));
        this.$store.commit("choose_psg_more_m", f2);
      }
      if (
        this.$store.state.choose_psg.length == 0 &&
        old_booker.psglist &&
        old_booker.psglist.length > 0
      ) {
        // console.log(1111);
        let f3 = JSON.parse(JSON.stringify([old_booker.psglist[0]]));
        this.$store.commit("choose_psg_m", f3);
      }
    },
    deletep (i) {
      showDialog({
        // title: "提示/Notice",
        message: "确定删除吗/Are you sure to delete?",
        theme: "round-button",
        confirmButtonText: "确定/Confirm", //改变确认按钮上显示的文字
        confirmButtonColor: "linear-gradient(160deg, #0274c9 0%, #044db5 100%)", //确定按钮文字
        showCancelButton: true,
        cancelButtonText: "取消/Cancel",
        cancelButtonColor:
          "linear-gradient(160deg, rgb(199 199 199) 0%, rgb(173 173 173) 100%)",
      })
        .then(() => {
          this.$store.state.choose_psg.splice(i, 1);
          this.$store.commit("delete_psg_more", i);
          // this.$store.commit("delete_psg_more", i);
          sessionStorage.setItem(
            "choose_psg",
            JSON.stringify(this.$store.state.choose_psg)
          );
        })
        .catch(() => { });
    },
    edit (i) {
      this.showPicker4 = false;
      let c = null;
      if (i || i == 0) {
        c = JSON.parse(JSON.stringify(this.$store.state.psglist[i]));
        this.title = "修改乘机人/Updated Profile";
      }
      else {
        c = {
          ID: null,
          FltPaxnameDom: "",
          IdNo: "",
          DeptName: "无",
          Sex: "", //
          FltPaxnameInt: "",
          MobileNo1: "",
          Birthday: "",
          CltId: null,
          idtype: "", //
          Authid: null,
          PreferOther: "",
          EmailCompany: "", //
        };
        this.title = "新增乘机人/increase Profile";
      }
      console.log(c);
      c.IdNo = crypto.decrypt(c.IdNo);
      this.eidt_p = c;
      this.showPicker5 = true;
    },
    sexx (s) {
      if (s == "M") return "男";
      else if (s == "F") return "女";
      else return;
    },
    on_save_psg (e) {
      let newpsg = JSON.parse(JSON.stringify(this.eidt_p));
      console.log(newpsg);

      let sex_j = { 男: "M", 女: "F", M: "M", F: "F" };
      let re = /^1\d{10}$/;
      let re1 = /^[0-9A-Za-z]{15,18}$/;
      if (!re.test(newpsg.MobileNo1)) {
        this.messConfirm_ui("请输入正确的手机号码");
        return;
      }
      if (newpsg.idtype == "身份证") {
        re1 = /^[0-9A-Za-z]{15,18}$/;
        if (
          !re1.test(newpsg.IdNo) ||
          newpsg.IdNo.indexOf("*") > 0 ||
          !(newpsg.IdNo.length == 15 || newpsg.IdNo.length == 18)
        ) {
          this.messConfirm_ui("请完整输入正确的身份证号");
          return;
        }
      }
      if (newpsg.idtype == "护照") {
        re1 = /^[0-9A-Za-z]{5,18}$/;
        console.log(1);
        if (!re1.test(newpsg.IdNo) || newpsg.IdNo.indexOf("*") > 0) {
          this.messConfirm_ui("请完整输入正确的护照号");
          return;
        }
      }
      if (newpsg.idtype == "大陆居民往来港澳通行证") {
        re1 = /^[0-9A-Za-z]{5,18}$/;
        if (!re1.test(newpsg.IdNo) || newpsg.IdNo.indexOf("*") > 0) {
          this.messConfirm_ui("请完整输入正确的大陆居民往来港澳通行证");
          return;
        }
      }
      if (newpsg.idtype == "港澳居民来往内地通行证") {
        re1 = /^[0-9A-Za-z]{5,18}$/;
        if (!re1.test(newpsg.IdNo) || newpsg.IdNo.indexOf("*") > 0) {
          this.messConfirm_ui("请完整输入正确的港澳居民来往内地通行证");
          return;
        }
      }
      if (newpsg.idtype == "台湾居民往来大陆通行证") {
        re1 = /^[0-9A-Za-z]{5,18}$/;
        if (!re1.test(newpsg.IdNo) || newpsg.IdNo.indexOf("*") > 0) {
          this.messConfirm_ui("请完整输入正确的台湾居民往来大陆通行证");
          return;
        }
      }
      if (newpsg.idtype == "港澳台居民居住证") {
        re1 = /^[0-9A-Za-z]{5,18}$/;
        if (!re1.test(newpsg.IdNo) || newpsg.IdNo.indexOf("*") > 0) {
          this.messConfirm_ui("请完整输入正确的台湾居民往来大陆通行证");
          return;
        }
      }
      if (newpsg.idtype == "其他证件类型") {
        re1 = /^[0-9A-Za-z]{5,18}$/;
        if (!re1.test(newpsg.IdNo) || newpsg.IdNo.indexOf("*") > 0) {
          this.messConfirm_ui("请完整输入正确的其他证件类型");
          return;
        }
      }
      if (!this.eidt_p.Identification) {
        newpsg.ID = this.$store.state.psglist.length + 1;
        newpsg.FltPaxnameDom = newpsg.FltPaxnameDom;
        newpsg.IdNo = newpsg.IdNo;
        newpsg.DeptName = "无";
        newpsg.Sex = sex_j[newpsg.Sex];
        newpsg.FltPaxnameInt = newpsg.FltPaxnameInt;
        newpsg.MobileNo1 = newpsg.MobileNo1;
        newpsg.Birthday = newpsg.Birthday ? newpsg.Birthday : "1970-01-01";
        newpsg.CltId = this.$store.state.psglist[0].CltId;
        newpsg.idtype = newpsg.idtype;
        newpsg.Authid = this.$store.state.psglist[0].Identification;
        newpsg.PreferOther = newpsg.PreferOther ? newpsg.PreferOther : "";
      }
      console.log("newpsg", newpsg);

      newpsg.IdNo = crypto.encrypt_up(newpsg.IdNo); //身份证加密
      this.save_p(newpsg).then((e) => {
        let result = e.data;
        console.log("result", result);

        if (result.indexOf("success") >= 0) {
          this.eidt_p.Identification = result.split("#")[1];
          // console.log(this.eidt_p.Identification);
          console.log(this.eidt_p);
          this.$store.commit("update_psg", this.eidt_p);
          let paxname = "";
          let companyjde = "";
          let g = axios({
            type: "GET",
            url:
              //获取乘客列表
              "https://timas.efasco.com/ErpService.asmx/GetPassengerBystaffnoAES?staff_no=" +
              sessionStorage.getItem("StaffNo") +
              "&paxname=" +
              paxname +
              "&companyjde=" +
              companyjde,
            cache: "false",
            dataType: "json",
          }).then((vv) => {
            console.log("vv", vv);
            this.$store.commit("setpsglist", vv.data.psglist);
            this.messConfirm_ui("新增成功/New succeed.");
          });
          this.showPicker5 = false;
        } else if (result.indexOf("diffname") >= 0) {
          this.messConfirm_ui(
            "由于您的个人信息与机票预定平台信息验证不一致,清联系TravelDesk确认您的个人信息"
          );
        } else {
          this.messConfirm_ui("保存失败");
        }
      });
    },
    async save_p (json) {
      console.log(json);
      return axios.post(
        "https://timas.efasco.com/ErpService.asmx/SavePassengerBystaffnoByAES",
        JSON.stringify(json).replace(/\+/g, "%2B").replace(/\&/g, "%26")
      );
    },
    add_psg () {
      console.log("add", this.$store.state.choose_psg_p);

      // let arr = this.dedupe(this.$store.state.choose_psg_p);
      // console.log(arr)
      //新添数据去重
      var o = [];
      for (var i = 0; i < this.$store.state.choose_psg_p.length; i++) {
        o[this.$store.state.choose_psg_p[i].$id] =
          this.$store.state.choose_psg_p[i];
      }
      console.log(o);
      this.$store.state.choose_psg_p = [];
      for (var el in o) {
        // console.log(el,o[el])
        if (el == o[el].$id) this.$store.state.choose_psg_p.push(o[el]);
      }

      console.log(this.$store.state.choose_psg_p);

      let f = JSON.parse(JSON.stringify(this.$store.state.choose_psg_p));

      this.$store.commit("choose_psg_m", f);
      sessionStorage.setItem(
        "choose_psg",
        JSON.stringify(this.$store.state.choose_psg)
      );
      this.showPicker4 = false;
    },
    get_this_list (item, key) {
      let a = item || [];
      if (!key) return a;
    },
    choose_idt (a) {
      this.eidt_p.idtype = a.selectedOptions[0].value;
      this.idtype_s = false;
    },
    change_idt (value) {
      // 证件类型的改变
      const idtype = value.selectedValues[0]
      if (idtype == "护照") {
        this.eidt_p.IdNo = crypto.decrypt(this.eidt_p.PassportNo)
        console.log('护照');
        console.log(this.eidt_p);
      } else if (idtype == "大陆居民往来港澳通行证") {
        console.log('大陆居民往来港澳通行证');
        this.eidt_p.IdNo = crypto.decrypt(this.eidt_p.HkmcEepNo)
        console.log(this.eidt_p);
      } else if (idtype == "港澳居民来往内地通行证") {
        console.log('港澳居民来往内地通行证');
        this.eidt_p.IdNo = crypto.decrypt(this.eidt_p.ReentryPermitNo)
        console.log(this.eidt_p);

      } else if (idtype == "台湾居民往来大陆通行证") {
        console.log('台湾居民往来大陆通行证');
        this.eidt_p.IdNo = crypto.decrypt(this.eidt_p.MtpNo)
        console.log(this.eidt_p);

      } else if (idtype == "港澳台居民居住证") {
        console.log('港澳台居民居住证');
        this.eidt_p.IdNo = crypto.decrypt(this.eidt_p.gatIdNo)
        console.log(this.eidt_p);

      } else if (idtype == "其他证件类型") {
        console.log('其他证件类型');
        this.eidt_p.IdNo = crypto.decrypt(this.eidt_p.OtherIdNo)
        console.log(this.eidt_p);

      } else {
        this.eidt_p.IdNo = crypto.decrypt(this.eidt_p.IdCardNo)
        console.log('身份证');
        console.log(this.eidt_p);

      }
    },
    messConfirm_ui (mes) {
      showDialog({
        // title: "提示/Notice",
        message: mes,
        theme: "round-button",
        confirmButtonText: "确定/Confirm", //改变确认按钮上显示的文字
        confirmButtonColor: "linear-gradient(160deg, #0274c9 0%, #044db5 100%)", //确定按钮文字
      });
    },
    choose_sex (a) {
      this.eidt_p.Sex = a.selectedOptions[0].value;
      this.idtype_sex = false;
      console.log(this.eidt_p);
    },
  }
}
</script>

